import {profileAPI, userAPI} from "../api/api";

const ADD_POST = "profile/ADD-POST";
const SET_USER_PROFILE = "profile/SET_USER_PROFILE";
const SET_STATUS = "profile/SET_STATUS";
const DELETE_POST = "profile/DELETE_POST";


let initialState = {
    postData: [
        {id: 1, message: "Hey, how are you?", likesCount: 23},
        {id: 2, message: "It is my first post", likesCount: 17},
    ],
    profile: null,
    status: ""
};

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let newMessage = action.newPostText;
            return {
                ...state,
                postData: [
                    ...state.postData,
                    {id: 5, message: newMessage, likesCount: 0},
                ],
            };

        case DELETE_POST:
            return {
                ...state, postData: state.postData.filter(p => p.id !== action.postId)
            };

        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile,
            };

        case SET_STATUS:
            return {
                ...state,
                status: action.status,
            };

        default:
            return state;
    }
};

export const addPostActionCreator = (newPostText) => ({type: ADD_POST, newPostText});

export const deletePostActionCreator = (postId) => ({type: DELETE_POST, postId});

export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});

export const setUserStatus = (status) => ({type: SET_STATUS, status});


export const setProfile = (userId) => async (dispatch) => {
    const response = await userAPI.getUserProfile(userId);
    dispatch(setUserProfile(response.data));
};

export const setStatus = (userId) => async (dispatch) => {
    const response = await profileAPI.getUserStatus(userId);
    dispatch(setUserStatus(response.data));
};

export const updateStatus = (status) => async (dispatch) => {
    const response = await profileAPI.updateUserStatus(status);
    if (response.data.resultCode === 0) {
        dispatch(setUserStatus(status));
    }
};


export default profileReducer;
