import React from "react";
import classes from "./Navbar.module.css";
import { NavLink } from "react-router-dom";

const Navbar = (props) => {

  let state = props.state;
  const slicedArray = state.sidebar.friends.slice(0, 3);
  let img = (
    <img
      src="https://mc.today/wp-content/uploads/2020/05/photo_2018-04-29_17-44-53.jpg"
      alt="dialog-avatar"
    />
  );
  let friends = slicedArray.map((friend) => (
    <li key={friend.id}>
      {img}
      <p>{friend.name}</p>
    </li>
  ));

  return (
    <nav className={classes.nav}>
      <ul>
        <li className={classes.item}>
          <NavLink to="/Profile" activeClassName={classes.activeLink}>
            Profile
          </NavLink>
        </li>
        <li className={classes.item}>
          <NavLink to="/Users" activeClassName={classes.activeLink}>
            Users
          </NavLink>
        </li>
        <li className={classes.item}>
          <NavLink to="/Dialogs" activeClassName={classes.activeLink}>
            Messages
          </NavLink>
        </li>
        <li className={classes.item}>
          <NavLink to="/Music" activeClassName={classes.activeLink}>
            Music
          </NavLink>
        </li>
        <li className={classes.item}>
          <NavLink to="/News" activeClassName={classes.activeLink}>
            News
          </NavLink>
        </li>
        <li className={classes.item}>
          <NavLink to="/Settings" activeClassName={classes.activeLink}>
            Settings
          </NavLink>
        </li> 
        <li className={classes.item}>
          <div className={classes.friendsItem}>
            Friends
            <ul>{friends}</ul>
          </div>
        </li>

      </ul>
    </nav>
  );
};

export default Navbar;
