import React from "react";
import styles from "./FormsControllers.module.css";



export const TextareaForPost = ({ input, meta: {touched, error}, ...props }) => {

    const hasError = error && touched;

  return (
    <div className={(hasError ? styles.postForm : "")}>
      <div>
        <textarea {...input} {...props} />
        { hasError && <div data-title={error} className={styles.errorSign + " " + styles.tooltip}><span>!</span></div>}
      </div>
      {/* { hasError && <span>{meta.error}</span>} */}
      
    </div>
  );
};


export const inputForLogin = ({ input, meta, ...props }) => {

  const hasError = meta.error && meta.touched;

return (
  <div className={(hasError ? styles.loginForm : "")}>
  <div>
    <input {...input} {...props} />
    { hasError && <div data-title={meta.error} className={styles.errorSign + " " + styles.tooltip}><span>!</span></div>}
  </div>
  {/* { hasError && <span>{meta.error}</span>} */}
  
</div>
);
};


export const TextareaForDialogs = ({ input, meta, ...props }) => {

  const hasError = meta.error && meta.touched;

return (
  <div className={(hasError ? styles.dialogsFrom : "")}>
      <div>
        <textarea {...input} {...props} />
        { hasError && <div data-title={meta.error} className={styles.errorSign + " " + styles.tooltip}><span>!</span></div>}
        <button>send</button>
      </div>
      {/* { hasError && <span>{meta.error}</span>} */}
      
    </div>
);
};
