import React from "react";
import { Field, reduxForm } from "redux-form";
import { login } from "../../redux/auth-reducer";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { inputForLogin } from "../Common/FormsControllers/FormsControllers";
import { required } from "../../utils/validators/validators";
import styles from "../Common/FormsControllers/FormsControllers.module.css"

const LoginForm = ({handleSubmit, error}) => {
  return (
    <form onSubmit={handleSubmit}>
      <div>
        <Field placeholder="Email" name={"email"} validate={[required]} component={inputForLogin} />
      </div>
      <div>
        <Field placeholder="password" name={"password"} validate={[required]} component={inputForLogin} />
      </div>
      <div>
        <Field type={"checkbox"} name={"rememberMe"} validate={[required]} component={inputForLogin} />{" "}
        remember me
      </div>
      {error && <div className={styles.commonFormError}>{error}</div>}
      <div>
        <button>Log in</button>
      </div>
    </form>
  );
};

const LoginReduxForm = reduxForm({
  form: "login",
})(LoginForm);

const Login = (props) => {
  const onSubmit = (formData) => {
    props.login(formData.email, formData.password, formData.rememberMe);
  };

  if (props.isAuth) {
    return <Redirect to={"/profile"} />;
  }

  return (
    <div>
      <h1>Login</h1>
      <LoginReduxForm onSubmit={onSubmit} />
    </div>
  );
};

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
});


export default connect(mapStateToProps, { login })(Login);
